import React from 'react'
import { SidebarContainer,
         Icon,
         CloseIcon,
         SidebarWrapper,
         SidebarMenu,
         SidebarLink,
         SideBtnWrap,
         SidebarRoute
        } from './SidebarElements' 

const Sidebar = ({isOpen,toggle}) => {
    return (
        <SidebarContainer isOpen={isOpen} onClick={toggle}>
            <Icon onClick={toggle}>
                <CloseIcon />
            </Icon>
            <SidebarWrapper>
                <SidebarMenu>
                    <SidebarLink onClick={toggle} to="about">
                        Sobre Mim
                    </SidebarLink>
                    <SidebarLink onClick={toggle} to="tecnology">
                        Tecnologias
                    </SidebarLink>
                    <SidebarLink onClick={toggle} to="project">
                        Projetos
                    </SidebarLink>
                    <SidebarLink onClick={toggle} to="contact">
                        Contato
                    </SidebarLink>
                </SidebarMenu>
               <SideBtnWrap>
                    {/* <SidebarRoute to='/'>
                        Sign In
                    </SidebarRoute> */}
                </SideBtnWrap>
            </SidebarWrapper>
        </SidebarContainer>
    )
}

export default Sidebar
