import React from 'react';
import {ProjectsContainer, 
        ProjectsH1, 
        ProjectsWrapper, 
        ProjectsCard, 
        ProjectsIcon, 
        ProjectsH2, 
        ProjectsP,
    TopLine} from '../Projects/ProjectsElements'
import icon1 from '../../images/pizza.png'
import icon2 from '../../images/netflix.png'
import icon3 from '../../images/port.png'
const Projects = () => {
    return (
        <ProjectsContainer id="projects">
            <TopLine>Um Pouco do que já fiz</TopLine>
            <ProjectsH1>Já viu algum dos meus Projetos?</ProjectsH1>
            <ProjectsWrapper>
                <ProjectsCard>
                    <ProjectsIcon src={icon1} />
                    <ProjectsH2>Pizzaria Camargo's</ProjectsH2>
                    <ProjectsP>Projeto de uma pizzaria com cardápio digital feito totalmente em React.
                    </ProjectsP>
                </ProjectsCard>
                <ProjectsCard>
                    <ProjectsIcon src={icon2}/>
                    <ProjectsH2>Clone Netflix</ProjectsH2>
                    <ProjectsP>Projeto de um clone da Netflix feito em React com manipulação de API.</ProjectsP>
                </ProjectsCard>
                <ProjectsCard>
                    <ProjectsIcon src={icon3}/>
                    <ProjectsH2>Meu Portfólio</ProjectsH2>
                    <ProjectsP>Projeto para um portfolio pessoal totalmente em React.</ProjectsP>
                </ProjectsCard>
                
            </ProjectsWrapper>
        </ProjectsContainer>
    )
}

export default Projects
