import React, {useState} from 'react'
import Video  from '../../video/video.mp4'
import {Button} from '../ButtonElements'

import { HeroContainer,
         HeroBg,
         VideoBg,
         HeroContent,
         HeroH1,
         HeroP,
         HeroBtnWrapper,
         ArrowForward,
         ArrowRight,

        } from './HeroElements'  


const HeroSection = () => {
    const [hover, setHover] = useState(false)

    const onHover = () =>{
        setHover(!hover)
    }

    return (
        <HeroContainer id="home">
            <HeroBg>
                <VideoBg autoPlay loop muted src={Video} type='video/mp4' />
            </HeroBg>
            <HeroContent>
                <HeroH1>Developer Front End and Web Designer</HeroH1>
                <HeroP>
                    Hello World! Me chamo João Gabriel <br/>
                    Sejam Todos Bem vindos ao meu mundo, <br/>
                    Onde todas as idéias saem da curva e do papel.
                </HeroP>
                <HeroBtnWrapper>
                    <Button to='projects' onMouseEnter={onHover} onMouseLeave={onHover}
                    onMouseLeave={onHover}
                    primary="true"
                    dark="true"
                    >
                        Vamos Lá? {hover ? <ArrowForward /> : <ArrowRight />}
                    </Button>
                </HeroBtnWrapper>
            </HeroContent>
        </HeroContainer>
    )
}

export default HeroSection
