import React from 'react'
import { FaGripfire } from "react-icons/fa";
import {FaBars} from 'react-icons/fa'
import {
    Nav,
    NavbarContainer,
    NavLogo,
    MobileIcon,
    NavMenu,
    NavItem,
    NavLinks,
  
} from './NavbarElements'

const Navbar = ({toggle}) => {
    return (
        <>
            <Nav>
                <NavbarContainer>
                    
                    <NavLogo to="home"><FaGripfire/>Gabriel Camargo Dev.</NavLogo>
                    <MobileIcon onClick={toggle}>
                        <FaBars />
                    </MobileIcon>
                    <NavMenu>
                        <NavItem>
                            <NavLinks to="about">Sobre Mim</NavLinks>
                        </NavItem>
                        <NavItem>
                            <NavLinks to="projects">Projetos</NavLinks>    
                        </NavItem>
                        <NavItem>
                            <NavLinks to="teclonogy">Tecnologias</NavLinks>
                        </NavItem>
                        <NavItem>
                            <NavLinks to="contact">Contato</NavLinks>
                        </NavItem>
                    </NavMenu>
                
                </NavbarContainer>
            </Nav>
        </>
    )
}

export default Navbar
